import './live-toast.styl'
const consolePrefix = '[Live Toast]'

/**
 * LiveToast .
 * @param {HTMLElement} element HTML , .
 * @param {string} content .
 * @param {string} type , "success" || "caution" || "error" || "info".
 */
function liveToast (element, content, type) {
  //  API,  type ,  'info'.
  if (typeof type === 'boolean') { type = 'info' }

  // Action:  Toast .
  var fragment = document.createDocumentFragment()
  var newToast = document.createElement('div')

  // Action:  Toast .
  type = type || 'info'
  if (type !== 'success' && type !== 'caution' && type !== 'error' && type !== 'info') {
    throw new Error(`${consolePrefix}  Live Toast : success || caution || error || info`)
  }

  newToast.innerHTML = `<i class="toast-icon ${type}"></i><span class="toast-text">${content}</span>`
  newToast.className = `live-toast a-move-in-bottom ${type}`

  if (!element.className && !element.attributes) {
    throw new Error(`${consolePrefix}  element .`)
  }

  // .
  const boundingClientRect = element.getBoundingClientRect()
  const elementLeft = boundingClientRect.left
  const elementTop = boundingClientRect.top
  const elementWidth = boundingClientRect.width
  const elementHeight = boundingClientRect.height

  //  X .
  var scrollLeft = (document.documentElement && document.documentElement.scrollLeft) || document.body.scrollLeft
  newToast.style.left = (elementLeft + elementWidth + scrollLeft) + 'px'

  //  Y .
  var scrollTop = (document.documentElement && document.documentElement.scrollTop) || document.body.scrollTop
  newToast.style.top = (elementTop + elementHeight + scrollTop) + 'px'

  // Toast .
  setTimeout(() => {
    newToast.className += ' out'
    setTimeout(() => {
      newToast.parentNode.removeChild(newToast)
    }, 380)
  }, 2000)

  fragment.appendChild(newToast)
  document.body.appendChild(fragment)

  //  Toast .
  const windowWidth = document.body.offsetWidth
  const toastLeft = newToast.getBoundingClientRect().left
  const toastWidth = newToast.offsetWidth

  if (windowWidth - toastWidth - toastLeft < 0) {
    newToast.style.left = (windowWidth - toastWidth - 10 + scrollLeft) + 'px'
  }
}

export default {
  install (Vue, options) {
    Vue.prototype.liveToast = liveToast
  }
}



