'use strict'

import Vue from 'vue'
import VueResource from '@bilibili-live/vue-resource'
import loginStatus from './login-status'

Vue.use(VueResource)

export default function liveQuickLogin () {
  if (!loginStatus()) {
    window.biliQuickLogin ? login() : Vue.http.jsonp('//static-s.bilibili.com/account/bili_quick_login.js').then(login, login)
  }
}

function login () {
  if (window.biliQuickLogin) {
    window.biliQuickLogin(() => window.location.reload())
    throw new Error('Bilibili Live: ~~ -_-')
  } else {
    throw new Error('Bilibili Live: .')
  }
}



