import Vue from 'vue'
import LivePopup from '@bilibili-live/component.popup'
import { randomEmoji } from '@bilibili-live/utils'
require('./Live-Cert-Popups.styl')

Vue.use(LivePopup)

const vm = new Vue({})

export default {
  // Definition: .
  cellphoneBinding: function () {
    vm.livePopup({
      title: '',
      html:
        '<div class="live-cert-popup cellphone-providing">' +
          '<div class="hint-img" role="image"></div>' +
          '<p class="t-center">' + randomEmoji.happy() + '</p>' +
        '</div>',
      width: 370,
      button: {
        confirm: '',
        cancel: ''
      },
      onConfirm: function () {
        window.location.href = 'https://passport.bilibili.com/member/bindPhone?gourl=http://live.bilibili.com'
      }
    })
  },

  // Definition: .
  roomExpired: function () {
    vm.livePopup({
      title: '',
      html:
      '<div class="live-cert-popup room-expired">' +
      '<div class="hint-img" role="image"></div>' +
      '<p class="t-center"></p>' +
      '<p class="t-center">' + randomEmoji.happy() + '</p>' +
      '</div>',
      width: 370,
      button: {
        confirm: '',
        cancel: ''
      },
      onConfirm: function () {
        window.location.href = 'https://account.bilibili.com/site/identification'
      }
    })
  },

  // Definition: .
  timingHint: function (timingText) {
    vm.livePopup({
      title: '',
      html:
      '<div class="live-cert-popup timing-hint">' +
        '<div class="hint-img" role="image"></div>' +
        '<p class="t-center"> ' + timingText + '  ' + randomEmoji.happy() + '</p>' +
        '<p class="t-center">' + randomEmoji.happy() + '</p>' +
      '</div>',
      width: 500,
      button: {
        confirm: '',
        cancel: '~'
      },
      onConfirm: function () {
        window.open('https://account.bilibili.com/site/identification')
        this.close()
      }
    })
  },

  // Definition: .
  trailClosed: function () {
    vm.livePopup({
      title: '',
      content: `~ ${randomEmoji.happy()}`,
      width: 400,
      button: {
        confirm: '',
        cancel: ''
      },
      onConfirm: function () {
        window.open('https://account.bilibili.com/site/identification')
        this.close()
      }
    })
  },

  // Definition: .
  failure: function (xhrObject) {
    vm.livePopup({
      title: '',
      html:
      '<p> > <</p>' +
      '<p><del style="color: #ccc"></del>... (`)</p>' +
      '<p></p>' +
      '<p>' + xhrObject.status + ' - ' + xhrObject.statusText + '</p>',
      width: 500,
      type: 'info',
      button: { confirm: '()(' }
    })
  }
}



