import EventBus from 'src/event-bus'
import eventName from './config.event-name'

const navConfig = [
  {
    text: '', href: '//www.bilibili.com'
  },
  {
    text: '', href: '/', drawer: true, arrowUp: 'areaNavPanel', target: '_self',
    mouseEnter () { EventBus.$emit(eventName.showAreaNavPanel) },
    mouseLeave () { EventBus.$emit(eventName.hideAreaNavPanel) }
  },
  {
    text: '', href: '/rank'
  },
  {
    text: '', href: '/liveHime', drawer: true, arrowUp: 'download',
    mouseEnter () { EventBus.$emit(eventName.showDownloadPanel) },
    mouseLeave () { EventBus.$emit(eventName.hideDownloadPanel) }
  }
]

export default navConfig



