export default class SeqAnimation {
  constructor ($els, displayMode, timing) {
    if (Object.prototype.toString.call($els) === '[object Array]') {
      this.elements = $els
    } else {
      this.elements = getSeqElements($els)
    }
    this.seqTiming = 0
    this.seqInTimeouts = []
    this.displayMode = displayMode
    this.deltaTiming = timing || 80
  }

  seqIn () {
    return new Promise(resolve => {
      this.reset()
      this.seqInTimeouts.forEach(value => clearTimeout(value))
      this.elements.forEach((element, index) => {
        this.seqInTimeouts[index] = setTimeout(() => { element.style.display = this.displayMode }, this.seqTiming)
        this.seqTiming += this.deltaTiming
      })
      setTimeout(resolve, this.deltaTiming * this.elements.length)
    })
  }

  reset () {
    this.elements.forEach(element => { element.style.display = 'none' })
    this.seqTiming = 0
  }
}

// Definition: .
//  "seqIn" .
function getSeqElements ($els) {
  const elements = Object.keys($els).filter(element => element.indexOf('seqIn') > -1).sort()
  var result = []
  elements.forEach(elementName => result.push($els[elementName]))
  return result
}



