/*
 *  Friendly Format By LancerComet at 16:00, 2015.11.20.
 *  # Carry Your Wolrd #
 *  ---
 *
 *  Description:
 *  ---
 *  .
 *  ,  10000 ,  100000 .
 *
 */
const chsUnit = '\u4e07'

export default {
  // Definition: .
  tenThousandFriendlyFormat (num) {
    return parseInt(num, 10) < 10000 ? num : friendlyFormat(num)
  },

  // Definition: .
  oneHundredThousandFormat (num) {
    return parseInt(num, 10) < 100000 ? num : friendlyFormat(num)
  }
}

// Definition: .
function friendlyFormat (num) {
  //  0  .
  if (!num) { return num }

  // , .
  if (isNaN(parseInt(num, 10))) {
    console.log(`FriendlyFormat:  ${num} , .`)
    return num
  }

  // .
  num = num.toString()

  //  'XX' .
  if (num.indexOf(chsUnit) > -1) {
    var pureNum = parseFloat(num.substr(0, num.indexOf(chsUnit)))
    if (pureNum < 10) { return num }
    var returnNum = pureNum.toFixed(1)
    return returnNum + chsUnit
  }

  if (num === 10000) {
    return `1.0${chsUnit}`
  } else if (num > 10000) {
    return (num / 10000).toString().substr(0, 3) + chsUnit
  } else {
    return num
  }

  /*
   *  '110000' => '10.1'
   *  110000 => '10.1'
   *  '10.1' => '10.1'
   *  '10' => '10.0'
   */
}



